.NewArrival{
    text-align: center;
    background-color: #f2f2f2;
    padding-bottom: 30px;
}

.NewArrival h1{
    font-size: 50px;
    font-family: dr;
    padding-top: 50px;
    padding-bottom: 5px;
    line-height: 40px;
}

.NewArrival p{
    font-size: 16px;
    font-family: mv;
    margin: 20px 0;
}

.item-c{
    margin: 10px auto;
    display: flex;
    width: 80%;
    justify-content: space-evenly;
}

.item{
    width: 30%;
    text-align: left;
    position: relative;
    height: 550px;
}

.item img{
    width: 100%;
    height: 300px;
}

.item h3{
    font-family: mv;
    margin: 30px 20px;
}

.item p{
    font-family: mi;
    color: #999999;
    margin: 30px 20px;
}

.item button{
    position: absolute;
    bottom: 0;
}