.About-c{
    width: 100%;
    display: flex;
    background-color: #f2f2f2;
    padding: 0 250px;
    justify-content: space-between;
    margin-top: 50px;
    height: 500px;
}

.left-c{
    width: 45%;
    position: relative;
}

.left-c img{
    width: 100%;
    position: absolute;
    top: -40px;
    height: 500px;
}

.right-c{
    width: 49%;
}

.right-c h1{
    font-family: dr;
    font-family: 100;
    font-size: 55px;
    margin: 10px 0;
    margin-top: 50px;
}

.right-c h4{
    font-family: mv;
    font-weight: 100;
    font-size: 21px;
    margin: 10px 0;
}

.right-c p{
    font-family: mi;
    font-size: 16.5px;
    margin: 10px 0;
    color: #999999 ;
}

.right-c button{
    margin: 10px 0;
}