.Sellection{
    width: 80%;
    display: flex;
    margin: 70px auto;
    margin-bottom: 100px;
}

.Left-c{
    width: 50%;
    position: relative;
}

.Img-c{
    height: 400px;
    width: 90%;
}

.Img-c img{
    position: absolute;
    height: 400px;
    width: 90%;
    top: 0;
}

.Left-c p{
    margin: 10px 20px;
    font-family: mi;
    color: #999999;
    width: 84%;
    font-size: 15px;
    line-height: 22px;
}

.Right-c{
    width: 50%;
    position: relative;
}

.Right-c h1{
    position: absolute;
    top: 30px;
    left: -40px;
    font-family: dr;
    font-size: 45px;
    line-height: 40px;
    width: 70%;
}

.Right-c img{
    position: absolute;
    top: 130px;
    left: 70px;
    height: 500px;
}