.Profile{
    width: 100%;
    background-color: #f2f2f2;
    position: relative;
    text-align: center;
    padding-top: 125px;
    padding-bottom: 20px;
    margin-top: 150px;
}

.Profile img{
    position: absolute;
    width: 170px;
    height: 170px;
    border-radius: 50%;
    top: -70px;
    left: 655px;
}

.Profile h1{
    font-family: dr;
    font-size: 52px;
    margin-bottom: 10px;
}

.Profile p{
    font-family: mv;
    font-size: 22px;
    margin: 10px;
}

.Profile button{
    margin: 20px;
}